.registerPage{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url("https://img.freepik.com/free-photo/top-view-business-office-desk-background-applying-job-form-pen-pencil-eyeglasses-tree-wooden-table-background-with-copy-space_1921-21.jpg?w=740&t=st=1684037579~exp=1684038179~hmac=2a2a2b090ee10e0894eb4f4836dba2bea037f78787f8affcccc7c262c98f7f9c");
    background-size: cover;
    width: 100vw;
    height: 100vh;
    object-fit: contain;
}
.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
     margin-top: 5rem;
     background: rgb(94, 100, 100);
     width: 30vw;
     height: 70vh;
     padding: 2rem;
     border-radius: 10px;
     box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
     align-items: center;
}
.btn{
    display: flex;
    flex-direction: column;
}
.form Input{
    width: 17rem;
}