.loginPage{
    width: 100vw;
    height: 100vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #dddfe2;
    background-image: url("https://img.freepik.com/free-vector/background-realistic-abstract-technology-particle_23-2148431735.jpg?w=740&t=st=1684033622~exp=1684034222~hmac=365844f439483ea8bb8109d0e28576f534037796cb20b59280749b35c51c90a9");
    background-size: cover;

}
.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    width: 30vw;
    height: 60vh;
    background:transparent;
    border-radius: 10px;
}
.btn{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}
.form Input{
    width: 15rem;
    border: 1px solid #dddfe2;
}
