.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
  border: 1px solid gray;
  width: 90vw;
  margin-left: 3rem;
  margin-top: 2rem;
}

.switch-icon{
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}
.progress{
  display: flex;

}